<template>
  <v-card :raised="true">
    <v-toolbar card color="purple darken-4">
      <v-toolbar-title>{{rule[0]}}</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon @click="toggleShow">
        <v-icon>{{show ? 'expand_less' : 'expand_more'}}</v-icon>
      </v-btn>
    </v-toolbar>
    <v-slide-y-transition>
      <v-card-text v-show="show" v-html="ruleContent">
      </v-card-text>
    </v-slide-y-transition>
  </v-card>
</template>

<script>
import Remarkable from 'remarkable'

const md = new Remarkable()

export default {
  name: 'Rule',
  props: {
    rule: Array,
    toggleShow: Function,
    show: Boolean,
  },
  computed: {
    ruleContent() {
      return md.render(this.rule.slice(1).join('\n'))
    }
  }
}
</script>

