<template>
  <v-app dark>
    <v-toolbar>
      <v-btn icon @click="goHome">
	<v-img src="https://nanotrasen.se/Images/Paradise2icon.PNG"/>
      </v-btn>

      <v-toolbar-title class="white--text">Server Rules</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon @click="toggleAll" title="Toggle all rules">
        <v-icon>{{!allEnabled ? 'add_circle_outline' : 'remove_circle_outline'}}</v-icon>
      </v-btn>
    </v-toolbar>
    <v-flex xs12 sm6 offset-sm3>
      <rule
         v-for="(rule, i) in rules"
         :key="rule[0]"
         :rule="rule"
         :style="$vuetify.breakpoint.smAndUp && {margin: '25px 0px'}"
         :toggleShow="toggleShow.bind(this, i)"
         :show="rulesEnabled[i]"/>
      </v-flex>
  </v-app>
</template>

<script>
import Rule from './components/Rule.vue'

function parseIntoSections(lines) {
  const result = []
  lines.forEach(line => {
    if (line && line[0]  === '#' && line.slice(1).indexOf('#') < 0) {
      result.push([line.slice(1).trim()])
      return
    }
    result[result.length - 1].push(line.trim())
  })
  return result
}

export default {
  name: 'App',
  components: {
    Rule,
  },
  data() {
    return {
      rules: null,
      rulesEnabled: null,
    }
  },
  created() {
    fetch('/RULES.md')
      .then(response => response.text())
      .then(text => {
        this.rules = parseIntoSections(text.split('\n'));
        this.rulesEnabled = new Array(this.rules.length).fill(false)
      })
  },
  computed: {
    allEnabled() {
      return this.rulesEnabled && this.rulesEnabled.every(rule => rule)
    }
  },
  methods: {
    goHome() {
      window.location = 'http://nanotrasen.se'
    },
    toggleShow(i) {
      this.rulesEnabled.splice(i, 1, !this.rulesEnabled[i])
    },
    toggleAll() {
      this.rulesEnabled = new Array(this.rules.length).fill(!this.allEnabled)
    }
  }
}
</script>
